.TextInput{
    display: flex;
    width: calc(100% - 32px);
    height: var(--height);
    padding: 16px;
    font-size: 14px;
    line-height: 20px;
    color: #252525;

    background: #f0848d74;
    border: 1px solid #FFFFFF;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    border-radius: 6px;

}